    margin: 0;
    margin-left: auto;
    margin-right: auto;
    padding: 0;
}

.projects__links {
    display: flex;
    justify-content: center;
    width: 40%;
    margin-bottom: 30px;
}

.projects__link {
    text-decoration: none;
    background-color: transparent;
    padding: 10px 0;
    color: white;
    font-weight: 400;
    cursor: pointer;
    width: 60px;
}

.projects__link_type_white {
    display: block;
}

.projects__link_type_black {
    display: none;
}

.projects__icon {
    width: 50px;
    transition: -webkit-transform 0.4s ease;
    transition: transform 0.4s ease;
    transition: transform 0.4s ease, -webkit-transform 0.4s ease;
}

.projects__icon:hover {
    -webkit-transform: translate(-3px, -3px);
            transform: translate(-3px, -3px);
}

@media (max-width: 666px) {
    .projects__subtitle {
        font-size: 23px;
    }
}

@media (max-width: 510px) {
    .projects__card {
        max-height: 700px;
        display: flex;
        flex-direction: column;
        background-color:rgba(233, 240, 243, 0.7);
        padding: 20px 0;
        border-radius: 25px;
    }
    .projects__card:hover {
        -webkit-transform: translate(0,0);
                transform: translate(0,0);
    }

    .projects__image-link {
        display: flex;
        justify-content: center;
    }

    .projects__image {
        width: 90%;
        margin: auto;
    }

    .projects__overlay {
        position: relative;
        opacity: 1;
        background: transparent;
    }

    .projects__text {
        color: black;
        padding: 5px 0;
        position: static;
        width: 90%;
        margin-top: 15px;
        margin-bottom: 10px;
    }

    .projects__links {
        margin-bottom: 0;
        width: 50%;
    }

    .projects__link_type_white {
        display: none;
    }

    .projects__link_type_black {
        display: block;
    }
}

.contact {
    text-align: center;
    padding: 250px 0;
    /* border-top: 1px solid rgba(0, 0, 0, 0.2); */
    width: 90%;
    margin: auto;
}

.contact__title {
    font-size: 50px;
    margin: 0;
    margin-bottom: 25px;
}

.contact__subtitle {
    font-size: 25px;
    margin-bottom: 65px;
}

.contact__button {
    border-radius: 0;
    -webkit-appearance: none;
    appearance: none;
    height: 50px;
    line-height: 50px;
    width: 350px;
    font-size: 32px;
    background-color: transparent;
    border: 3px solid #7cd5df;
    border-radius: 65px;
    padding: 10px 55px;
    color: #7cd5df;
    font-weight: 400;
    transition: 0.4s ease;
    text-decoration: none;
}

.contact__button:hover {
    color: white;
    /* background-color: #3ac0cf; */
    background-color: #7cd5df;
    cursor: pointer;
}

@media (max-width: 666px) {
    .contact__title {
        font-size: 40px;
        margin: 0;
    }

    .contact__subtitle {
        font-size: 20px;
        margin-bottom: 25px;
    }
}

@media (max-width: 510px) {
    .contact {
        width: 90%;
        margin: auto;
        padding: 200px 0;
    }

    .contact__title {
        font-size: 30px;
    }

    .contact__button {
        width: 250px;
        height: 30px;
        line-height: 30px;
        font-size: 15px;
   }
}

@media (max-width: 400px) {
    .contact {
        padding: 150px 0;
    }

    .contact__title {
        font-size: 28px;
        margin: 0;
    }

    .contact__subtitle {
        font-size: 15px;
        margin-bottom: 25px;
    }
}
.footer {
    width: 100%;
    height: 100px;
    background-color: #232323;
    padding: 10px 0;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.footer__text {
    color: white;
    text-align: center;
    font-size: 20px;
    margin: 0;
    margin-bottom: 10px;
}

.footer__text:last-child {
    margin-bottom: 0;
}

@media (max-width: 510px) {
    .footer {
        height: 75px; 
    }

    .footer__text {
        font-size: 10px;
        margin-bottom: 3px;
    }
}
